.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.slider {
  height: 700px;
  background-image: url('../public/img/cslider.jpg');
  background-size: cover;
  background-position: center;
  margin-top: 17px;
}

.SB {
  /* background-color: #61dafb; */
  height: 2000px;
}

.SB>.container {
  background-color: white;
  height: 130px;
  min-width: 1902px;
  margin-top: 38px;
}

.SB>.container>.abc {
