.swiper {
  width: 100%;
  height: 800px;
  position: relative;
  margin-top: 50px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* background: gold; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.SB>.container5>.insta {
